.product-stat {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-bottom: 10px;
  align-items: center;
}

.product-stat__name {
  flex: 0 0 215px;
  margin-bottom: 5px;

  display: flex;
  align-items: center;
  flex-flow: row nowrap;
}

.product-stat__name img {
  width: 18px;
  height: 18px;
  margin-right: 16px;
}

.full-bar {
  flex: 1 1 200px;
  min-width: 200px;
  background-color: #2D2032;
}

.full-bar span{
  display: block;
}

.stat-bar-filled {
  text-align: center;
}

.stat-hp {
  color: #ED7AA1;
}

.stat-hp .stat-bar-filled {
  background-color: #ED7AA1;
  color: #FFF;
}

