.dropdown {
  position: relative;
}

.dropdown__menu {
  position: absolute;
  transform-origin: top right;
  right: 0;
  color: #FFF;
  background-color: #2f273c;
  border: 2px solid #211b22;
  padding: 16px 16px;
  margin-top: 6px;
  border-radius: 6px;
}

.dropdown__menu p {
  margin-bottom: 6px;
}

.dropdown__menu p:first-child {
  text-align: center;
}

.dropdown__closer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.font-awesome {
  color: black;
  font-size: 16px;
}

.dropdown img{
  width:100%;
  background: #a17e25;
}

.dropdown__menu button {
  margin-top: 6px;
  font-size: var(--text-lg);
  color: #FFF;
  padding: 0;
  border: none;
  background: none;
}

.dropdown__menu button:hover {
  text-decoration: underline;
}

.cart-icon__container {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  position: relative;
  padding: 6px;
}

.cart-icon__container a {
  display:block;
  fill: currentColor;
  color: #FFF;
  text-decoration: none;
  font-weight: var(--font-bold);
}

.cart-icon__container a:hover {
  color: #a17e25;
}

.cart-icon__container a > span {
  display: inline-block;
  margin-top: 4px;
}

.cart-icon__quantity {
  display: block;
  position: absolute;
  top: -5px;
  right: 6px;
  padding: 3px 5px;

  border: 2px solid #000;
  border-radius: 6px;
  color: #FFF;
  background-color: #CA2533;
}

.cart-icon__quantity span {
  display: block;
  font-size: var(--text-sm);
  font-weight: var(--font-black);
}

.main-header {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 12px;
  margin-top: 12px;
  grid-gap: 16px;
  gap: 16px;
}

.shop-sign {
  order: 2;
}
.shop-sign a {
  display: block;
}

.shop-sign img {
  display: block;
  width: 100%;
  margin-bottom: -80px;
}

.header-group {
  display: flex;
  flex-flow: row;
  align-items: center;
  grid-gap: 16px;
  gap: 16px;
}

.user-account {
  width: 58px;
  height: 58px;
  background: #211B22;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.total-credit {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2px 15px;

  font-size: var(--text-2xl);
  font-weight: var(--font-bold);

  height: 40px;
  min-width: 100px;
  background-color: #211B22;
  border-radius: 999px; /*Should remain pill shape regardless of how big we set the height of the pill to.*/
}

.total-credit img {
  display: block;
  width: 24px;
  padding-right: 15px;
}

@media screen and (min-width: 360px) {
  .shop-sign {
    max-width: 350px;
  }
}

@media screen and (min-width: 730px) {
  .main-header {
    justify-content: center;
  }
  .shop-sign {
    max-width: 250px;
    order: 0;
  }
}

@media screen and (min-width: 780px) {
  .main-header {
    justify-content: space-between;
  }
}



