.product-filter-container {
  /* display: flex;
  flex-direction: column; */
  max-width: 70px;
}

.svg-filter-button {
  width: 100%;
}

.out-circle {
  stroke: #070608;
  stroke-width: 1px;
  fill: #443648;
}
.in-circle {
  fill: #57455d;
}
.up-triangle {
  fill: #443648;
}

